html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0 0 0.67em;
}

h2, h3, h4, h5, h6, p {
  margin-top: 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  color: inherit;
  text-decoration: none;
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

:root {
  --color-red: #ff4b55;
  --color-grey: #c4c4c4;
}

.theme-light {
  --theme-fg: #1a1a1a;
  --theme-bg: #fff;
  --theme-accent: #004e93;
  --theme-border: rgba(26, 26, 26, .3);
}

.theme-dark {
  --theme-fg: #fff;
  --theme-bg: #1a1a1a;
  --theme-accent: #6CACE4;
  --theme-border: rgba(255, 255, 255, .3);
}

html {
  color: var(--theme-fg);
  font-size: 16px;
  font-weight: 400;
  height: 100%;
  background-color: var(--theme-bg);
  scroll-behavior: smooth;
}

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background-color: var(--theme-bg);
  transition: 0.2s;
}
.header--sticky {
  box-shadow: 0 0 25px var(--color-grey);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
}
.header--sticky + main {
  padding-top: 60px;
}

.header__container {
  max-width: 1080px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 15px;
}
@media (min-width: 768px) {
  .header__container {
    padding-right: 40px;
    padding-left: 40px;
  }
}

.nav {
  margin-left: auto;
  margin-right: 15px;
}
@media (min-width: 768px) {
  .nav {
    margin-right: 30px;
  }
}

.nav__list {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding-left: 0;
}

.nav__item {
  margin-right: 15px;
}
.nav__item:last-child {
  margin-right: 0;
}
@media (min-width: 768px) {
  .nav__item {
    margin-right: 30px;
  }
}

.nav__link {
  padding-bottom: 5px;
  position: relative;
  transition: 0.2s;
}
.nav__link::before {
  content: "";
  width: 0;
  height: 1px;
  background-color: currentColor;
  position: absolute;
  top: 100%;
  left: 50%;
  transition: inherit;
}
.nav__link:hover::before, .nav__link:focus-visible::before {
  width: 100%;
  left: 0;
}

.toggle-theme {
  color: inherit;
  width: 24px;
  height: 24px;
  padding: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
  overflow: hidden;
  transition: 0.2s;
}
.toggle-theme--dark .toggle-theme__icon-dark {
  opacity: 1;
}
.toggle-theme--dark .toggle-theme__icon-light {
  opacity: 0;
}

.toggle-theme__icon-light {
  opacity: 1;
}

.toggle-theme__icon-dark {
  opacity: 0;
}

.main__container {
  max-width: 1080px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}
@media (min-width: 768px) {
  .main__container {
    padding-right: 40px;
    padding-left: 40px;
  }
}

.intro {
  padding-top: 30px;
  padding-bottom: 30px;
  border-bottom: 1px solid var(--theme-border);
}
@media (min-width: 768px) {
  .intro {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
@media (min-width: 992px) {
  .intro {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}
@media (min-width: 1440px) {
  .intro {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.intro__lead {
  font-size: 2rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0;
}

.works {
  padding-top: 30px;
  padding-bottom: 30px;
  border-bottom: 1px solid var(--theme-border);
}
@media (min-width: 768px) {
  .works {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
@media (min-width: 992px) {
  .works {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}
@media (min-width: 1440px) {
  .works {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.works__title {
  font-size: 1.5rem;
}
@media (max-width: 767px) {
  .works__title {
    text-align: center;
  }
}

.works__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
  gap: 30px 15px;
}
@media (min-width: 768px) {
  .works__list {
    gap: 30px;
  }
}

.work__img-link {
  display: block;
  margin-bottom: 15px;
  border-radius: 6px;
  box-shadow: 0 0 25px rgba(208, 208, 208, 0.25);
  transition: 0.2s;
}
.work__img-link:hover, .work__img-link:focus-visible {
  box-shadow: 0 0 25px rgba(208, 208, 208, 0.75);
}
@media (min-width: 768px) {
  .work__img-link {
    margin-bottom: 30px;
  }
}

.work__img {
  width: 100%;
  max-height: 280px;
  border-radius: inherit;
  object-fit: cover;
  object-position: top center;
}

.work__inner-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.work__title {
  margin-bottom: 0;
}

.work__title-link {
  position: relative;
  transition: 0.2s;
}
.work__title-link::before {
  content: "";
  width: 0;
  height: 1px;
  background-color: #ff4b55;
  position: absolute;
  top: 100%;
  left: 0;
  transition: 0.2s;
}
.work__title-link:hover::before, .work__title-link:focus-visible::before {
  width: 100%;
}

.work__social-icon {
  display: block;
}

.work__description {
  margin-bottom: 5px;
}

.work__hint {
  color: var(--theme-accent);
}

.experience {
  padding-top: 30px;
  padding-bottom: 30px;
  border-bottom: 1px solid var(--theme-border);
  display: grid;
  gap: 15px;
}
@media (min-width: 768px) {
  .experience {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
@media (min-width: 992px) {
  .experience {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}
@media (min-width: 1440px) {
  .experience {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}
@media (min-width: 768px) {
  .experience {
    grid-template-columns: 1fr 3fr;
  }
}

.experience__title {
  font-size: 1.5rem;
  margin-bottom: 0;
}
@media (max-width: 767px) {
  .experience__title {
    text-align: center;
  }
}

.experience__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.experience__item:not(:last-child) {
  margin-bottom: 30px;
}

.experience__item-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 10px;
}

.experience__item-title {
  font-size: 1.5rem;
  font-weight: 400;
  margin-bottom: 0;
}

.experience__item-customer {
  display: inline-block;
  color: var(--theme-accent);
  margin-bottom: 10px;
  position: relative;
}
.experience__item-customer::before {
  content: "";
  width: 0;
  height: 1px;
  background-color: currentColor;
  position: absolute;
  top: 100%;
  right: 0;
  transition: 0.2s;
}
.experience__item-customer:hover::before, .experience__item-customer:focus-visible::before {
  width: 100%;
  right: auto;
  left: 0;
}

.experience__text:last-child {
  margin-bottom: 0;
}

.footer {
  padding-top: 30px;
  padding-bottom: 45px;
}
@media (min-width: 768px) {
  .footer {
    padding-top: 50px;
    padding-bottom: 65px;
  }
}
@media (min-width: 1440px) {
  .footer {
    padding-top: 70px;
    padding-bottom: 90px;
  }
}

.footer__title {
  text-align: center;
}

.footer__container {
  max-width: 1080px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}
@media (min-width: 768px) {
  .footer__container {
    padding-right: 40px;
    padding-left: 40px;
  }
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
}
@media (min-width: 768px) {
  .social {
    gap: 30px;
  }
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 15px;
  min-width: 210px;
  border: 1px solid #c6c6c6;
  border-radius: 4px;
  box-sizing: border-box;
  transition: 0.2s;
}
.social__link:hover, .social__link:focus-visible {
  color: var(--theme-bg);
  background-color: var(--theme-fg);
  border-color: var(--theme-fg);
}
@media (max-width: 767px) {
  .social__link {
    flex-grow: 1;
  }
}

.social__icon {
  margin-right: 10px;
}